
import { defineComponent, reactive } from 'vue';
import Adjacent from '@/components/Adjacent.vue'; // @ is an alias to /src
import { useRoute } from 'vue-router'; 

export default defineComponent({
  name: 'Obj',
  components: {
    Adjacent,
  },
  setup(){
    //data
    const data = reactive({
      panel_fmid:""
    })

    const route = useRoute();
    data.panel_fmid = (route.params.fmid) as string;

    return{
      data
    }
  }
});
